<template>
	<canvas
		:id="canvasId"
		:height="height"
		:width="width"
		@mousemove="handleHover"
		@mousedown="handleMouseDown"
		@mouseup="handleMouseUp"
		@contextmenu="handleRightClick"
		@mouseenter="showTooltip"
		@mouseleave="hideTooltip" />
	<tooltip
		:x="tooltipX"
		:y="tooltipY"
		:visible="tooltipVisible"
		tooltip-id="compressor-map-tooltip">
		<div>Boost:</div><div>{{ (Math.round(pointerData.boost * 10) / 10).toFixed(2) }} PSI</div>
		<div>rpm:</div><div>{{ pointerData.rpm }}</div>
		<div>maf:</div><div>{{ (Math.round(pointerData.maf["lbs/min"] * 10) / 10).toFixed(1) }} lbs/min</div>
		<div>compr. power:</div><div>{{ pointerData.compressorPower["kilowatt"].toFixed(3) }} kWatts</div>
	</tooltip>
</template>

<script lang="ts">
import { defineComponent, computed, onMounted, watch, ref, onBeforeUnmount } from "vue"
import { useStore } from "vuex";
import { buildAccessor as buildGridAccessor } from "@/store/grid";
import { Grid } from "./graphics";
import {
	buildMapPointsGetter,
	buildAccessor as buildEngineMapAccessor,
	buildMapPointModifier,
	buildMapPointInserter,
	buildMapPointRemover,
} from "@/store/engine-map";
import { useGridWatchers } from "./grid-watchers";
import { MassAirflow, Power, Pressure, PressureUnit, Temperature } from "@/physics/units";
import { getCompressorPower, getMAF, getRPMfromMAF } from "@/physics/calculations";
import { useMouseControl } from "./mouse-control";
import Tooltip from "@/components/Tooltip.vue";

type EngineMapProps = {
	width: number;
	height: number;
	canvasId: string;
	padding: number;
}

export default defineComponent({
	name: "Map",
	components: { Tooltip },
	props: {
		height: Number,
		width: Number,
		canvasId: String,
		padding: Number,
	},
	setup: (props) => {
		const { canvasId, height, width, padding } = props as EngineMapProps;
		const mapWidth = width - 2 * padding
		const mapHeight = height - 2 * padding

		const tooltipX = ref(0)
		const tooltipY = ref(0)
		const tooltipVisible = ref(false)

		const setTooltipPosition = (x: number, y: number) => {
			tooltipX.value = x;
			tooltipY.value = y;
		}
		const hideTooltip = () => tooltipVisible.value = false;
		const showTooltip = () => tooltipVisible.value = true;

		let graphCanvas: HTMLCanvasElement;
		const graphCanvasViewport = ref<DOMRect>({ left: 0, top: 0 } as DOMRect);
		const windowResizeHandler = () => { graphCanvasViewport.value = graphCanvas?.getBoundingClientRect() }
		const originX = computed(() => graphCanvasViewport.value.left + padding)
		const originY = computed(() => graphCanvasViewport.value.top + padding)

		const store = useStore();

		const minAirflow = computed(buildGridAccessor("compressorGrid", "minX", store));
		const maxAirflow = computed(buildGridAccessor("compressorGrid", "maxX", store));
		const airflowInterval = computed(buildGridAccessor("compressorGrid", "xStep", store));
		const minPressureRatio = computed(buildGridAccessor("compressorGrid", "minY", store));
		const maxPressureRatio = computed(buildGridAccessor("compressorGrid", "maxY", store));
		const pressureRatioStep = computed(buildGridAccessor("compressorGrid", "yStep", store));
		const airflowUnits = computed(buildEngineMapAccessor("airflowUnits", store));

		const engineDisplacement = computed(buildEngineMapAccessor("displacement", store));
		const ambientAirPressure = computed(buildEngineMapAccessor("ambientAirPressure", store));
		const ambientAirTemperature = computed(buildEngineMapAccessor("ambientAirTemperature", store));
		const pointerData = ref({
			boost: 0,
			rpm: 0,
			compressorPower: new Power(0),
			maf: new MassAirflow(0)
		});

		const setMapPoint = buildMapPointModifier(store);
		const insertMapPoint = buildMapPointInserter(store);
		const removeMapPoint = buildMapPointRemover(store);

		const correctMaf = (maf: number): number => {
			return maf
				* Math.sqrt(ambientAirTemperature.value.SI / new Temperature(288.15).SI)
				* (1 / ambientAirPressure.value[PressureUnit.bar])
		}

		const getMAFlocal = ({ pressure, rpm }: { pressure: Pressure; rpm: number }): number => {
			const actualMaf = getMAF({
				displacement: engineDisplacement.value,
				temperature: ambientAirTemperature.value,
				pressure,
				rpm,
			})

			const correctedMaf = new MassAirflow(correctMaf(actualMaf.SI));

			return correctedMaf[airflowUnits.value];
		}

		const engineMapRawPoints = computed(buildMapPointsGetter(store));
		const engineMapPoints = computed(() => {
			return [
				{
					x: getMAFlocal({
						pressure: ambientAirPressure.value,
						rpm: engineMapRawPoints.value[0]?.rpm
					}),
					y: minPressureRatio.value
				},
				...engineMapRawPoints.value.map(p => ({
					x: getMAFlocal({
						pressure: p.pressure,
						rpm: p.rpm
					}),
					y: p.pressure.getPressureRatio(ambientAirPressure.value)
				})),
				{
					x: getMAFlocal({
						pressure: ambientAirPressure.value,
						rpm: engineMapRawPoints.value[engineMapRawPoints.value.length - 1]?.rpm
					}),
					y: minPressureRatio.value
				}
			]
		})

		onMounted(() => {
			graphCanvas = document.getElementById(canvasId) as HTMLCanvasElement;
			graphCanvasViewport.value = graphCanvas.getBoundingClientRect()
			window.addEventListener("resize", windowResizeHandler);
			window.addEventListener("scroll", windowResizeHandler);

			const graph = new Grid({
				canvasRef: graphCanvas,
				height,
				width,
				padding,
				minX: minAirflow.value,
				maxX: maxAirflow.value,
				minY: minPressureRatio.value,
				maxY: maxPressureRatio.value
			});

			graph.drawMap(engineMapPoints.value)

			useGridWatchers({
				grid: graph,
				gridActions: [() => { graph.drawMap(engineMapPoints.value) }],
				minX: minAirflow,
				maxX: maxAirflow,
				xStep: airflowInterval,
				minY: minPressureRatio,
				maxY: maxPressureRatio,
				yStep: pressureRatioStep
			})

			watch(
				() => engineMapPoints.value,
				(points) => { graph.drawMap(points) }
			)
		});

		onBeforeUnmount(() => {
			window.removeEventListener("resize", windowResizeHandler);
			window.removeEventListener("scroll", windowResizeHandler);
		})

		let contextPoint = -1;

		const getRPM = ({ massAirflow, pressureRatio }: { massAirflow: MassAirflow; pressureRatio: number }): number => Math.round(getRPMfromMAF({
			massAirflow,
			displacement: engineDisplacement.value,
			temperature: ambientAirTemperature.value,
			pressure: ambientAirPressure.value.multiply(pressureRatio)
		}))

		const findMapPointIndex = (rpm: number, pressure: Pressure): number => {
			return engineMapRawPoints.value.findIndex((point) => {
				if (point.rpm === rpm || (point.rpm * 1.05 > rpm && point.rpm * 0.95 < rpm)) {
					if (
						point.pressure.SI === pressure.SI ||
						(point.pressure.SI * 1.01 > pressure.SI && point.pressure.SI * 0.99 < pressure.SI)
					) {
						return true;
					}
				}

				return false;
			})
		}

		const {
			handleHover,
			handleMouseDown,
			handleMouseUp,
			handleRightClick
		} = useMouseControl({
			originX,
			originY,
			mapWidth,
			mapHeight,
			padding,
			minX: minAirflow,
			maxX: maxAirflow,
			minY: minPressureRatio,
			maxY: maxPressureRatio,
			transformYValue: a => a,
			transformXValue: (maf: number) => new MassAirflow(maf, airflowUnits.value),
			onHover: (massAirflow: MassAirflow, pressureRatio: number, x, y) => {
				setTooltipPosition(x - graphCanvasViewport.value.left, y - graphCanvasViewport.value.top)
				const rpm = getRPM({ massAirflow, pressureRatio })

				pointerData.value = {
					rpm,
					boost: ambientAirPressure.value.multiply((pressureRatio - 1))["psi"],
					compressorPower: getCompressorPower({
						massAirflow,
						ambientTemperature: ambientAirTemperature.value,
						pressureRatio,
						compressorEfficiency: 0.72
					}),
					maf: massAirflow,
				}

				if (contextPoint > -1) {
					setMapPoint(
						{
							rpm,
							pressure: ambientAirPressure.value.multiply(pressureRatio)
						},
						contextPoint
					)
				}
			},
			onMouseDown: (massAirflow: MassAirflow, pressureRatio: number) => {
				const rpm = getRPM({ massAirflow, pressureRatio })
				const pressureAtMouse = ambientAirPressure.value.multiply(pressureRatio)

				contextPoint = findMapPointIndex(rpm, pressureAtMouse);
			},
			onMouseUp: () => { contextPoint = -1 },
			onRightClick: (massAirflow: MassAirflow, pressureRatio: number) => {
				const rpm = getRPM({ massAirflow, pressureRatio })
				const pressureAtMouse = ambientAirPressure.value.multiply(pressureRatio)

				const pointIndex = findMapPointIndex(rpm, pressureAtMouse);

				if (pointIndex < 0) {
					insertMapPoint({
						rpm,
						pressure: new Pressure(Math.round(pressureAtMouse["bar"] * 100) / 100, PressureUnit.bar)
					});
				} else {
					removeMapPoint(engineMapRawPoints.value[pointIndex]);
				}
			}
		})

		return {
			handleHover,
			handleMouseDown,
			handleMouseUp,
			handleRightClick,
			pointerData,
			airflowUnits,
			tooltipX,
			tooltipY,
			tooltipVisible,
			showTooltip,
			hideTooltip,
		}
	},
})
</script>

