<template>
	<canvas
		:id="canvasId"
		:height="height"
		:width="width" />
</template>

<script lang="ts">
import { defineComponent, computed, onMounted } from "vue"
import { useStore } from "vuex";
import { buildAccessor as buildGridAccessor } from "@/store/grid";
import { Grid } from "./graphics";
import { useGridWatchers } from "./grid-watchers";

type GridProps = {
	width: number;
	height: number;
	canvasId: string;
	padding: number;
	name: "compressorGrid" | "turbineGrid";
}

export default defineComponent({
	name: "Grid",
	props: {
		height: Number,
		width: Number,
		canvasId: String,
		padding: Number,
		name: String,
	},
	setup: (props) => {
		const { canvasId, padding, height, width, name } = props as GridProps;
		const store = useStore();

		const minX = computed(buildGridAccessor(name, "minX", store));
		const maxX = computed(buildGridAccessor(name, "maxX", store));
		const xStep = computed(buildGridAccessor(name, "xStep", store));
		const minY = computed(buildGridAccessor(name, "minY", store));
		const maxY = computed(buildGridAccessor(name, "maxY", store));
		const yStep = computed(buildGridAccessor(name, "yStep", store));

		onMounted(() => {
			const gridCanvas = document.getElementById(canvasId) as HTMLCanvasElement;

			const grid = new Grid({
				canvasRef: gridCanvas,
				height,
				width,
				padding,
				minX: minX.value,
				maxX: maxX.value,
				minY: minY.value,
				maxY: maxY.value,
				xInterval: xStep.value,
				yInterval: yStep.value
			});

			const redrawGrid = () => {
				grid.drawGrid();
				grid.drawAxis();
			}

			redrawGrid();

			useGridWatchers({
				grid,
				gridActions: [redrawGrid],
				minX,
				maxX,
				xStep,
				minY,
				maxY,
				yStep
			})
		});
	},
})
</script>
