
import { defineComponent, computed, onMounted, watch } from "vue"
import { useStore } from "vuex";
import { buildAccessor as buildGridAccessor } from "@/store/grid";
import { Grid } from "./graphics";
import { buildAccessor as buildEngineMapAccessor } from "@/store/engine-map";
import { useGridWatchers } from "./grid-watchers";
import { MassAirflow, MassAirflowUnits, Temperature } from "@/physics/units";
import { buildInterpolator, getTurbinePower, uncorrectGasflow } from "@/physics/calculations";
import { buildMapPointsGetter as buildTurbineMapPointsGetter } from "@/store/turbine-model";
import { CP_AIR_288K, GAMMA_AIR_288K } from "@/physics/constants";

type EngineMapProps = {
	width: number;
	height: number;
	canvasId: string;
	padding: number;
}

export default defineComponent({
	name: "TurbineMapOnCompressor",
	props: {
		height: Number,
		width: Number,
		canvasId: String,
		padding: Number,
	},
	setup: (props) => {
		const { canvasId, height, width, padding } = props as EngineMapProps;

		let graphCanvas: HTMLCanvasElement;
		const store = useStore();

		const minAirflow = computed(buildGridAccessor("compressorGrid", "minX", store));
		const maxAirflow = computed(buildGridAccessor("compressorGrid", "maxX", store));
		const airflowInterval = computed(buildGridAccessor("compressorGrid", "xStep", store));
		const minPressureRatio = computed(buildGridAccessor("compressorGrid", "minY", store));
		const maxPressureRatio = computed(buildGridAccessor("compressorGrid", "maxY", store));
		const pressureRatioStep = computed(buildGridAccessor("compressorGrid", "yStep", store));

		const ambientAirTemperature = computed(buildEngineMapAccessor("ambientAirTemperature", store));

		const turbineMapPoints = computed(buildTurbineMapPointsGetter(store));

		const interpolateTurbinePower = buildInterpolator(
			() => turbineMapPoints.value,
			({ gasflow, pressureRatio }) => uncorrectGasflow({
				correctedFlow: new MassAirflow(gasflow, MassAirflowUnits.lbsmin),
				pressureRatio,
				referenceTemperature: new Temperature(288.15),
				exhaustTemperature: new Temperature(1100)
			})["lbs/min"],
			({ gasflow, pressureRatio }) => getTurbinePower({
				massAirflow: uncorrectGasflow({
					correctedFlow: new MassAirflow(gasflow, MassAirflowUnits.lbsmin),
					pressureRatio,
					referenceTemperature: new Temperature(288.15),
					exhaustTemperature: new Temperature(1100)
				}),
				pressureRatio,
				inletTemperature: new Temperature(1100),
				turbineEfficiency: 0.69
			}).SI,
		)

		const impossiblePressures = (maf: number): number => {
			const afr = 1 + (1 / 11.3);
			const a = interpolateTurbinePower(maf * afr) * 0.60
			const b = new MassAirflow(maf, MassAirflowUnits.lbsmin).SI * CP_AIR_288K * ambientAirTemperature.value.SI;
			const c = (a / b) + 1;

			return Math.pow(c, GAMMA_AIR_288K / (GAMMA_AIR_288K - 1));
		}

		onMounted(() => {
			graphCanvas = document.getElementById(canvasId) as HTMLCanvasElement;

			const graph = new Grid({
				canvasRef: graphCanvas,
				height,
				width,
				padding,
				minX: minAirflow.value,
				maxX: maxAirflow.value,
				minY: minPressureRatio.value,
				maxY: maxPressureRatio.value
			});

			graph.plotFunction(impossiblePressures)

			useGridWatchers({
				grid: graph,
				gridActions: [() => graph.plotFunction(impossiblePressures)],
				minX: minAirflow,
				maxX: maxAirflow,
				xStep: airflowInterval,
				minY: minPressureRatio,
				maxY: maxPressureRatio,
				yStep: pressureRatioStep
			})

			watch(
				() => turbineMapPoints.value.reduce((agg, p) => agg + p.pressureRatio + p.gasflow, 0),
				() => { graph.plotFunction(impossiblePressures) }
			)
		});
	},
})
