import { Ref } from "vue"

type MouseEventHandler<T, K> = (xValue: T, yValue: K, x: number, y: number) => void

type MouseControlConfig<XUnit, YUnit> = {
	originX: Ref<number>;
	originY: Ref<number>;
	mapWidth: number;
	mapHeight: number;
	padding: number;
	minX: Ref<number>;
	maxX: Ref<number>;
	minY: Ref<number>;
	maxY: Ref<number>;
	transformXValue: (input: number) => XUnit;
	transformYValue: (input: number) => YUnit;
	onHover: MouseEventHandler<XUnit, YUnit>;
	onMouseDown: MouseEventHandler<XUnit, YUnit>;
	onMouseUp: MouseEventHandler<XUnit, YUnit>;
	onRightClick: MouseEventHandler<XUnit, YUnit>;
}

export const useMouseControl = <X, Y>({
	originX,
	originY,
	mapHeight,
	mapWidth,
	minX,
	maxX,
	minY,
	maxY,
	transformXValue,
	transformYValue,
	onHover,
	onMouseDown,
	onMouseUp,
	onRightClick,
}: MouseControlConfig<X, Y>) => {
	const getMapX = (mapX: number): number => ((mapX / mapWidth) * (maxX.value - minX.value)) + minX.value
	const getMapY = (mapY: number): number => ((1 - (mapY / mapHeight)) * (maxY.value - minY.value)) + minY.value

	const getMapDataFromAtCanvasXY = ({ x, y }: { x: number; y: number }): [number, number] => {
		const mapY = getMapY(y - originY.value)
		const mapX = getMapX(x - originX.value)

		const result: [number, number] = [
			mapX,
			mapY,
		]

		if (mapX > maxX.value) { result[0] = maxX.value }
		if (mapX < minX.value) { result[0] = minX.value }
		if (mapY > maxY.value) { result[1] = maxY.value }
		if (mapY < minY.value) { result[1] = minY.value }

		return result;
	}

	const handleMouseEvent = (e: MouseEvent): [X, Y, number, number] => {
		const [ xValue, yValue ] = getMapDataFromAtCanvasXY(e);

		return [
			transformXValue(xValue),
			transformYValue(yValue),
			e.x,
			e.y
		];
	}

	const handleHover = (e: MouseEvent) => onHover(...handleMouseEvent(e))
	const handleMouseDown = (e: MouseEvent) => onMouseDown(...handleMouseEvent(e));
	const handleMouseUp = (e: MouseEvent) => onMouseUp(...handleMouseEvent(e));
	const handleRightClick = (e: MouseEvent) => {
		e.preventDefault();
		onRightClick(...handleMouseEvent(e));
	}

	return {
		handleHover,
		handleMouseDown,
		handleMouseUp,
		handleRightClick
	}
}